
import { computed } from 'vue'
import { useRoute } from 'vue-router'
import Header from '@/components/Header.vue'
import Loading from '@/components/Loading.vue'

export default {
  components: {
    Header,
    Loading
  },
  setup() {
    const route = useRoute()
    const fullPath = computed(() => route.fullPath)
    return { fullPath }
  }
}
