
import { mapGetters } from 'vuex'

export default {
  name: 'Bike List',
  computed: {
    ...mapGetters({
      lang: 'app/lang'
    })
  },
  props: ['arr'],
  setup(props: any, { emit }: any) {
    const handdleRow = (row: any) => {
      emit('getSnoChart', {
        sbi: Number(row.sbi),
        tot: Number(row.tot),
        lat: Number(row.lat),
        lng: Number(row.lng),
        sna: row.sna,
        snaen: row.snaen
      })
    }
    return {
      handdleRow
    }
  }
}
